import React, {Component} from 'react';
import CKEditor from "react-ckeditor-component";
import Draggable from 'react-draggable';
import firebase from "./../firebase";
import moment from 'moment'; 
import $ from 'jquery';

export default class Todocontent extends Component {
    constructor(props) {
        super(props);
        this.state = {
            todos:[],
            todostab: false,
            projects:[],
            projectstab: true,
            lastidp: 0,
            lastidt: 0
        }
    }

    componentDidMount(){
        //alert(JSON.stringify(this.props.history));
        this.getTodo ();
        this.getProjects ();
    }

    getTodo () {
        //alert(this.state.messages)
        var todoDB = firebase
          .database()
          .ref("/todo")
          .limitToLast(500)
          todoDB.on("value", snapshot => {
          let newTodo = [];
          snapshot.forEach(child => {
            var todo = child.val();
            newTodo.push({ id: child.key, content: todo.content,time: todo.time,userid: todo.userid })
          })
          this.setState({ todos: newTodo });
          this.setState({ lastidt: this.state.todos.length });
          //alert(this.state.lastid);
        })
      }

      getProjects () {
        //alert(this.state.messages)
        var projectsDB = firebase
          .database()
          .ref("/projects")
          .limitToLast(500)
          projectsDB.on("value", snapshot => {
          let newProjects = [];
          snapshot.forEach(child => {
            var project = child.val();
            newProjects.push({ id: child.key, content: project.content,time: project.time,userid: project.userid })
          })
          this.setState({ projects: newProjects });
          this.setState({ lastidp: this.state.projects.length });
          //alert(this.state.lastid);
        })
      }

      projectsadd () {
        //this.setState({
        //  notes: this.state.notes.slice(-1)[0]+1
        //});
        this.state.projects.push(this.state.projects.slice(-1)[0]+1);
        this.forceUpdate();
        this.writeNewProjectsToDB('New Projects');
        //alert(JSON.stringify(this.state.notes));
      }

      todosadd () {
        //this.setState({
        //  notes: this.state.notes.slice(-1)[0]+1
        //});
        this.state.todos.push(this.state.todos.slice(-1)[0]+1);
        this.forceUpdate();
        this.writeNewTodosToDB('New Todos');
        //alert(JSON.stringify(this.state.notes));
      }

      onChangep(evt){
    
        //console.log("onChange fired with event info: ", evt);
        var newContent = evt.editor.getData();
    
        localStorage.setItem('ckeditcontentp', JSON.stringify(newContent));
      }

      onChanget(evt){
    
        //console.log("onChange fired with event info: ", evt);
        var newContent = evt.editor.getData();
    
        localStorage.setItem('ckeditcontentt', JSON.stringify(newContent));
      }

      projectsdelete(message) {
        let updateData = firebase.database().ref("/projects").child(message);
        updateData.remove();
      }

      todosdelete(message) {
        let updateData = firebase.database().ref("/todo").child(message);
        updateData.remove();
      }

      writeNewProjectsToDB = message => {
        var curtime = moment().format("DD/MM/YYYY HH:mm:ss");
        //alert(curtime);
        firebase
          .database()
          .ref("/projects")
          .push({
            id: this.state.lastidp+1,
            content: message,
            time: curtime,
            userid: localStorage.getItem('user_id')
          })
      }

      writeNewTodosToDB = message => {
        var curtime = moment().format("DD/MM/YYYY HH:mm:ss");
        //alert(curtime);
        firebase
          .database()
          .ref("/todo")
          .push({
            id: this.state.lastidt+1,
            content: message,
            time: curtime,
            userid: localStorage.getItem('user_id')
          })
      }

      writeProjectsToDB(message,id) {
        var curtime = moment().format("DD/MM/YYYY HH:mm:ss");
        
        let updateData = firebase.database().ref("/projects").child(message);
        updateData.child("content").set(localStorage.getItem('ckeditcontentp').replace(/"/g, ""));
        updateData.child("time").set(curtime);
      }

      writeTodosToDB(message,id) {
        var curtime = moment().format("DD/MM/YYYY HH:mm:ss");
        
        let updateData = firebase.database().ref("/todo").child(message);
        updateData.child("content").set(localStorage.getItem('ckeditcontentt').replace(/"/g, ""));
        updateData.child("time").set(curtime);
      }

      projectstab(){
        this.setState({projectstab: true});
        this.setState({todostab: false});
      }

      todostab(){
        this.setState({projectstab: false});
        this.setState({todostab: true});
      }

    render(){
        const sta={
            cursor: 'pointer',
            color: 'white',
            backgroundColor: '#7386D5'
        }
        const stab={
            backgroundColor: 'silver'
        }
        return (<div>
        <ul className="nav nav-tabs">
               <li className="nav-item" style={sta}>
                 <a className={"nav-link " + (this.state.projectstab?'active':'')} onClick={() => this.projectstab()}>Projects</a>
        {this.state.projectstab === true && (<div  style={stab}><button className="btn btn-primary" onClick={() => this.projectsadd()}>Projects Add</button></div>)}
        {this.state.projectstab === true && (<div className="row" style={stab}>
           {this.state.projects.map((project, idx) => {
               
                    return (
                    <Draggable
                    defaultPosition={{x: 10, y: 10}}
                    defaultclassNameName="col-4"
                    > 
                        <div className="card col-4">
                            <div className="card-header">
                                Projects <button className="btn btn-primary float-right" onClick={() => this.projectsadd()}>+</button><button className="btn btn-danger float-right" onClick={() => this.projectsdelete(project.id)}><i className="fa fa-trash"></i></button>
                            </div>
                            <div className="card-body">
                                            <CKEditor 
                                                activeClass={"p10-"+project.id}
                                                content={project.content} 
                                                events={{
                                                    "blur": this.onBlur,
                                                    "afterPaste": this.afterPaste,
                                                    "change": this.onChangep
                                                }}
                                                />
                            </div>
                            <div className="card-footer">
                              <button className="btn btn-primary" onClick={() => this.writeProjectsToDB(project.id,idx)}>Save&nbsp;<i className="fa fa-save"></i></button>
                              <p>Last update: {project.time}</p>
                            </div>
                        </div>
                    </Draggable>
                )
            //}
        })}
        </div>)}</li>
        <li className="nav-item" style={sta}>
        <a className={"nav-link " + (this.state.todostab ? 'active' : '')} onClick={() => this.todostab()}>Todo</a>
        {this.state.todostab===true && (<div style={stab}><button className="btn btn-primary" onClick={() => this.todosadd()}>Todos Add</button></div>)}
       {this.state.todostab===true && (<div className="row" style={stab}>
           {this.state.todos.map((todo, idx) => {
               
                    return (
                    <Draggable
                    defaultPosition={{x: 10, y: 10}}
                    defaultclassNameName="col-4"
                    > 
                        <div className="card col-4">
                            <div className="card-header">
                                Todo <button className="btn btn-primary float-right" onClick={() => this.todosadd()}>+</button><button className="btn btn-danger float-right" onClick={() => this.todosdelete(todo.id)}><i className="fa fa-trash"></i></button>
                            </div>
                            <div className="card-body">
                                            <CKEditor 
                                                activeClass={"p10-"+todo.id}
                                                content={todo.content} 
                                                events={{
                                                    "blur": this.onBlur,
                                                    "afterPaste": this.afterPaste,
                                                    "change": this.onChanget
                                                }}
                                                />
                            </div>
                            <div className="card-footer">
                              <button className="btn btn-primary" onClick={() => this.writeTodosToDB(todo.id,idx)}>Save&nbsp;<i className="fa fa-save"></i></button>
                              <p>Last update: {todo.time}</p>
                            </div>
                        </div>
                    </Draggable>
                )
            //}
        })}
        </div>)}</li>
        </ul></div>);
    }
}