import React, {Component} from "react";
import CKEditor from "react-ckeditor-component";
import Draggable from 'react-draggable';
import firebase from "./../firebase";
import moment from 'moment'; 
import $ from 'jquery';

export default class Edit extends Component {
  constructor(props) {
    super(props);
    this.updateContent = this.updateContent.bind(this);
    this.onChange = this.onChange.bind(this);
        this.state = {
            notes:[],
            lastid: 0,
            ckeditcontent:''
        }
  }

  componentDidMount(){
        //CKEditor.config = 'Save,Print,Preview,Find,Maximize,ShowBlocks';
        CKEditor.config = {'toolbar':'document'};
        CKEditor.config.removePlugins = 'Save,Print,Preview,Find,About,Maximize,ShowBlocks';
        this.getNotes();
  }

  updateContent(newContent) {
    this.setState({
        content: newContent
    });
  }

  onChange(evt){
    
    //console.log("onChange fired with event info: ", evt);
    var newContent = evt.editor.getData();
    //this.setState({
    //  ckeditcontent: newContent
    //});
    //console.log(JSON.stringify(this.state.ckeditcontent));
    //alert(JSON.stringify(this.state.ckeditcontent));
    //alert(JSON.stringify(newContent));
    //evt.preventDefault();
    localStorage.setItem('ckeditcontent', JSON.stringify(newContent));
  }

  //shouldComponentUpdate(nextProps, nextState) {
  //  if (this.props.ckeditcontent !== nextProps.ckeditcontent) {
  //    return false;
  //  } else {
  //    return true;
  //  }
  //}

  onBlur(evt){
    console.log("onBlur event called with event info: ", evt);
  }

  afterPaste(evt){
    console.log("afterPaste event called with event info: ", evt);
  }

  notesadd () {
    //this.setState({
    //  notes: this.state.notes.slice(-1)[0]+1
    //});
    this.state.notes.push(this.state.notes.slice(-1)[0]+1);
    this.forceUpdate();
    this.writeNewNotesToDB('New Notes');
    //alert(JSON.stringify(this.state.notes));
  }

  getNotes () {
    //alert(this.state.messages)
    var notesDB = firebase
      .database()
      .ref("/notes")
      .limitToLast(500)
      notesDB.on("value", snapshot => {
      let newNotes = [];
      snapshot.forEach(child => {
        var note = child.val();
        newNotes.push({ id: child.key, content: note.content,time: note.time,userid: note.userid })
      })
      this.setState({ notes: newNotes });
      this.setState({ lastid: this.state.notes.length });
      //alert(this.state.lastid);
    })
  }

  writeNewNotesToDB = message => {
    var curtime = moment().format("DD/MM/YYYY HH:mm:ss");
    //alert(curtime);
    firebase
      .database()
      .ref("/notes")
      .push({
        id: this.state.lastid+1,
        content: message,
        time: curtime,
        userid: localStorage.getItem('user_id')
      })
  }

  writeNotesToDB(message,id) {
    var curtime = moment().format("DD/MM/YYYY HH:mm:ss");
    //alert(curtime);
    //firebase
    //  .database()
    //  .ref("/notes")
    //  .push({
    //    id: this.state.lastid+1,
    //    content: message,
    //    time: curtime,
    //    userid: localStorage.getItem('user_id')
    //  })
    //firebase
    //  .database()
    //  .ref("/notes")
    //  .child(message)
    //  .child("content").setValue("new status")
    //  .child("times").setValue(curtime);

    //alert(JSON.stringify(this.state.ckeditcontent));
    //console.log(CKEditor.instances);
    let updateData = firebase.database().ref("/notes").child(message);
    updateData.child("content").set(localStorage.getItem('ckeditcontent').replace(/"/g, ""));
    updateData.child("time").set(curtime);
  }

  notedelete(message) {
    let updateData = firebase.database().ref("/notes").child(message);
    updateData.remove();
  }
  render() {
       return (<div><button className="btn btn-primary" onClick={() => this.notesadd()}>Notes Add</button>
       <div className="row">
           {this.state.notes.map((note, idx) => {
               //if( note % 4 === 0){
               // return ('') 
               //} else{

              //Notes {note.id} {note.id.id} {idx} <button className="btn btn-primary" onClick={() => this.notesadd()}>+</button><button className="btn btn-danger" onClick={() => this.notedelete(note.id)}><i className="fa fa-trash"></i></button>
                    return (
                    <Draggable
                    defaultPosition={{x: 10, y: 10}}
                    defaultclassNameName="col-4"
                    > 
                        <div className="card col-4">
                            <div className="card-header">
                                Notes <button className="btn btn-primary float-right" onClick={() => this.notesadd()}>+</button><button className="btn btn-danger float-right" onClick={() => this.notedelete(note.id)}><i className="fa fa-trash"></i></button>
                            </div>
                            <div className="card-body">
                                            <CKEditor 
                                                activeClass={"p10-"+note.id}
                                                content={note.content} 
                                                events={{
                                                    "blur": this.onBlur,
                                                    "afterPaste": this.afterPaste,
                                                    "change": this.onChange
                                                }}
                                                />
                            </div>
                            <div className="card-footer">
                              <button className="btn btn-primary" onClick={() => this.writeNotesToDB(note.id,idx)}>Save&nbsp;<i className="fa fa-save"></i></button>
                              <p>Last update: {note.time}</p>
                            </div>
                        </div>
                    </Draggable>
                )
            //}
        })}
        </div></div>);
    }
}